import React, { Component } from "react";
import {
  FiCast,
  FiLayers,
  FiUsers,
  FiMonitor,
  FiPhone,
  FiPackage,
  FiPlayCircle,
  FiSmartphone,
} from "react-icons/fi";

const ServiceList = [
  {
    icon: <FiCast />,
    title: "System Design",
    description:
      "Architecting the large scalable systems based on the business needs, a professional on serverless architectures.",
  },
  {
    icon: <FiLayers />,
    title: "API Development",
    description:
      "Developing API(s) to expose the data to internal tools and expose to 3rd party vendors",
  },
  {
    icon: <FiUsers />,
    title: "Web Application Development",
    description:
      "Design, develop and maintiains web apps for internal tools and vendor accessible tools",
  },
  {
    icon: <FiSmartphone />,
    title: "Mobile App Development",
    description: "Apps for smart phones with trending technologies",
  },
  {
    icon: <FiUsers />,
    title: "Sharing Knowladge",
    description:
      "Tech talks, training teams or individuals to enhance the industrial practices",
  },
];

class ServiceThree extends Component {
  render() {
    const { column } = this.props;
    const ServiceContent = ServiceList.slice(0, this.props.item);

    return (
      <React.Fragment>
        <div className="row">
          {ServiceContent.map((val, i) => (
            <div className={`${column}`} key={i}>
              <a href="">
                <div className="service service__style--2">
                  <div className="icon">{val.icon}</div>
                  <div className="content">
                    <h3 className="title">{val.title}</h3>
                    <p>{val.description}</p>
                  </div>
                </div>
              </a>
            </div>
          ))}
        </div>
      </React.Fragment>
    );
  }
}
export default ServiceThree;
